
























import { Component, Mixins, Prop } from 'vue-property-decorator'
import { Swipe, SwipeItem } from 'vant'

import baseComponent from 'components/mixins/baseComponent'
import baseField from 'components/mixins/baseField'

@Component({
  name: 'insideBanner',
  components: {
    vanSwipe: Swipe,
    vanSwipeItem: SwipeItem
  }
})
export default class insideBanner extends Mixins(baseComponent) {
  @Prop({ type: Array, default: () => [] }) fieldList!: Array<any>

  // 方向 默认横向
  private get configType(): string {
    return this.configData.type || ''
  }

  // 指示器 默认 显示
  private get configShowIndicators(): boolean {
    return this.configData.showIndicators || true
  }

  // 指示器颜色 默认 显示
  private get configIndicatorColor(): string {
    return this.configData.indicatorColor || 'white'
  }

  // 自动轮播间隔 默认3000
  private get configAutoplay(): number | string {
    return this.configData.autoplay || 3000
  }

  private swipeFun(url: string) {
    if (url) window.location.href = url
  }
}
