import { isNull } from 'plugin/helper'

const presets: presets = {
  // 正则校验
  regex: (value, failMsg, content) => {
    if (value && content) {
      const reg = new RegExp(content)
      if (!reg.test(value)) {
        return new Error(failMsg)
      }
    }
  },

  // 是否必填
  require: (value, failMsg) => {
    if (isNull(value)) {
      return new Error(failMsg)
    }
  },

  // 校验姓名
  name: (value, failMsg) => {
    const reg = new RegExp('^[a-zA-Z\u4E00-\u9FA5\uf900-\ufa2d·•.⦁·．˙s]{2,20}$')

    if (value && !reg.test(value)) {
      return new Error(failMsg)
    }
  },

  // 校验手机号码
  phone: (value, failMsg) => {
    const reg = new RegExp(/^1[3|4|5|6|7|8|9][0-9]{9}$/)
    if (value && !reg.test(value)) {
      return new Error(failMsg)
    }
  },

  // 校验固话
  fixedLine: (value, failMsg) => {
    const reg = new RegExp(/^0\d{2,3}-?\d{5,8}$/)
    if (value && !reg.test(value)) {
      return new Error(failMsg)
    }
  },

  // 校验手机号码或固话
  fixOrPhone: (value, failMsg) => {
    const reg = new RegExp(/^((0\d{2,3}-?\d{5,8})|(1[3456789]\d{9}))$/)
    if (value && !reg.test(value)) {
      return new Error(failMsg)
    }
  },

  // 校验身份证
  idCard: (value, failMsg) => {
    const reg = new RegExp(
      '^(1[1-5]{1}|2[1-3]{1}|3[1-7]{1}|4[1-6]{1}|5[0-4]{1}|6[1-5]{1}|71|8[1-2]{1}|91)[0-9]{4}(19|20)[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|30|31)|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}([0-9]|x|X)$'
    )
    if (value && !reg.test(value)) {
      return new Error(failMsg)
    }
  },

  // 校验特殊字符
  message: (value, failMsg) => {
    const reg = new RegExp('^[\u4e00-\u9fa5A-Za-z0-9--_——，～、：.·“”（）,~-·""()–•⦁_]{1,50}$')
    if (value && !reg.test(value)) {
      return new Error(failMsg)
    }
  },

  // 只能是数字
  number: (value, failMsg) => {
    const reg = new RegExp(/^([0-9])+$/)
    if (value && !reg.test(value)) {
      return new Error(failMsg)
    }
  },

  // 只能是英文
  english: (value, failMsg) => {
    const reg = new RegExp(/^([A-Za-z])+$/)
    if (value && !reg.test(value)) {
      return new Error(failMsg)
    }
  },

  // 只能是英文和数字
  numberOrEn: (value, failMsg) => {
    const reg = new RegExp(/^([A-Za-z]|[0-9])+$/)
    if (value && !reg.test(value)) {
      return new Error(failMsg)
    }
  },

  // 车牌号校验
  carNumber: (value, failMsg) => {
    const reg = new RegExp('^[a-zA-Z0-9\u4E00-\u9FA5]{7,8}$')
    if (value && !reg.test(value)) {
      return new Error(failMsg)
    }
  },

  // 校验省
  province: (value, failMsg) => {
    if (value && value.length !== 1) {
      return new Error(failMsg)
    }
  },

  // 校验省市
  provinceCity: (value, failMsg) => {
    if (value && value.length !== 2) {
      return new Error(failMsg)
    }
  },

  // 校验省市区
  provinceCityDistrict: (value, failMsg) => {
    if (value && value.length !== 3) {
      return new Error(failMsg)
    }
  },

  // 远程校验 跳过
  remote: () => undefined
}

export default presets
