
















import { Vue, Component, Prop, Watch, Ref } from 'vue-property-decorator'
import { Checkbox } from 'vant'

@Component({
  name: 'globalCheckbox',
  components: {
    vanCheckbox: Checkbox
  }
})
export default class globalCheckbox extends Vue {
  @Ref('checkbox') checkbox!: any

  @Prop({ type: Boolean }) value!: boolean

  // 是否是方形的
  @Prop({ type: Boolean, default: false }) square!: boolean

  // 大小设置
  @Prop({ type: String, default: '20' }) iconSize!: string

  private insideValue: boolean = false

  @Watch('value', { immediate: true })
  private valueHandler() {
    this.insideValue = this.value
  }

  @Watch('insideValue')
  private insideValueHandler() {
    this.$emit('input', this.insideValue)
  }

  public toggle(value: boolean) {
    this.checkbox.toggle(value)
  }
}
