







import { Vue, Component, Prop, Watch } from 'vue-property-decorator'

import jsbarcode from 'jsbarcode'

let uuid: number = 0

@Component({
  name: 'insideBarCode'
})
export default class insideBarCode extends Vue {
  @Prop({ type: [String, Number], required: true }) value!: string | number
  @Prop({ type: Object, default: () => ({}) }) options!: jsbarcode.BaseOptions
  @Prop({ type: [String] }) text!: string

  private uuid: number = 0

  private get baseOptions(): jsbarcode.BaseOptions {
    const defaultOptions: jsbarcode.BaseOptions = {
      margin: 0,
      displayValue: false
    }

    return {
      ...defaultOptions,
      ...this.options
    }
  }

  @Watch('value', { immediate: true })
  private valueHandler() {
    this.newJsbarcode()
  }

  private newJsbarcode() {
    this.$nextTick(() => {
      jsbarcode(`#bar-code__${this.uuid}`, String(this.value), this.baseOptions)
    })
  }

  // uuid 自增
  private created() {
    uuid = uuid + 1
    this.uuid = uuid
  }
}
