
























import { Component, Mixins, Prop } from 'vue-property-decorator'
import { Cell } from 'vant'

import baseComponent from 'components/mixins/baseComponent'
import baseField from 'components/mixins/baseField'

@Component({
  name: 'insideCard',
  components: {
    vanCell: Cell
  }
})
export default class insideCard extends Mixins(baseComponent, baseField) {
  private get configToPageIndex() {
    return this.configData.toPageIndex
  }

  // 背景颜色
  private get configBackgroundColor(): string {
    return this.configData.backgroundColor || '#2284ff'
  }

  private clickEvent() {
    this.dispatch('baseOrderForm', { event: 'order-form-next', params: [this.configToPageIndex] })
  }
}
