

























import { Vue, Component, Prop, Watch, Ref } from 'vue-property-decorator'
import { Dialog, Icon } from 'vant'

@Component({
  name: 'insideExampleDialog',
  components: {
    vanDialog: Dialog.Component,
    vanIcon: Icon
  }
})
export default class insideExampleDialog extends Vue {
  @Prop({ type: String }) title!: string

  @Prop({ type: String }) description!: string

  @Prop({ type: String }) imageUrl!: string

  private show: boolean = false

  public open() {
    this.show = true
  }

  public close() {
    this.show = false
  }
}
