








































import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
import { Icon } from 'vant'

@Component({
  name: 'insideStep',
  components: {
    vanIcon: Icon
  }
})
export default class insideStep extends Vue {
  @Prop({ type: String }) name!: string
  @Prop({ type: String }) type!: string
  @Prop({ type: [String, Number] }) title!: string | number
  @Prop({ type: [String, Number] }) text!: string | number
  @Prop({ type: [String, Number] }) desc!: string
  @Prop({ type: Boolean, default: true }) showIcon!: boolean

  private insideType: string = this.type
  private isActive: boolean = false

  private get insideStepClass() {
    const type = this.isActive ? 'success' : this.insideType
    const classMap: { [k: string]: boolean } = {
      'inside-step__wrapper': true,
      'inside-step__horizontal': this.direction === 'horizontal',
      'inside-step__vertical': this.direction === 'vertical',
      'inside-step__mail': this.direction === 'mail'
    }
    if (type) {
      classMap[`inside-step__${type}`] = true
    }
    return classMap
  }

  // 图标
  private get iconByType(): string {
    const iconMap: { [k: string]: string } = {
      success: 'success',
      error: 'cross',
      wran: 'fail',
      await: 'clock-o'
    }

    return this.isActive ? 'success' : iconMap[this.insideType] || ''
  }

  // 显示方向
  private get direction(): string {
    return (this.$parent as any).direction
  }

  private setIsActive(active: boolean) {
    this.isActive = active
  }

  private mounted(): void {
    this.$parent.$emit('step.add', this)
  }

  private beforeDestroy(): void {
    this.$parent.$emit('step.remove', this)
  }
}
