












import { Vue, Component, Prop } from 'vue-property-decorator'
import { Icon } from 'vant'

@Component({
  name: 'insideInfoCell',
  components: {
    vanIcon: Icon
  }
})
export default class insideInfoCell extends Vue {
  @Prop({ type: [String, Number] }) value!: string | number
  @Prop({ type: String }) label!: string
  @Prop({ type: Boolean, default: false }) isLink!: boolean
  @Prop({ type: String, default: 'arrow' }) iconName!: string
  @Prop({ type: String, default: '' }) valueColor!: string
  private get insideInfoPanel() {
    return this.$parent
  }

  private mounted(): void {
    this.insideInfoPanel.$emit('info.panel.add', this)
  }

  private beforeDestroy(): void {
    this.insideInfoPanel.$emit('info.panel.remove', this)
  }
}
