














import { Vue, Component, Watch } from 'vue-property-decorator'
import { checkBrowser } from 'plugin/helper'
import { Action, Getter } from 'vuex-class'

import backHeader from './components/backHeader.vue'
import floatMenu from './components/floatMenu.vue'

@Component({
  name: 'Layout',
  components: {
    backHeader,
    floatMenu
  }
})
export default class Layout extends Vue {
  @Action('app/addCacheView') addCacheViewAction!: (view: string) => void

  @Getter('app/cacheViews') cacheViews!: Array<string>

  // 是否需要底部
  isNeedFloatMenu: boolean = true

  get key() {
    if (!this.$route.meta.cache) {
      const name = this.$route.name || ''

      const random = (min: number, max: number): number => {
        const range = max - min
        const rand = Math.random()
        const num = min + Math.round(rand * range)
        return num
      }

      return name + new Date().getTime() + random(1, 1000)
    }
  }

  get headerBackConfig() {
    if (checkBrowser() === 'PSBC') return { color: '#29b105', show: true }
    return { color: '#2284ff', show: false }
  }

  @Watch('$route', { immediate: true })
  routerHandler() {
    const name = this.$route.name || ''

    // 是否需要 floatMenu
    if (typeof this.$route.meta.floatMenu === 'boolean') {
      this.isNeedFloatMenu = this.$route.meta.floatMenu
    } else {
      this.isNeedFloatMenu = true
    }

    if (this.$route.meta.cache) {
      this.$nextTick(() => {
        this.addCacheViewAction(name)
      })
    }
  }
}
