import moment from 'moment'

class InPrototype {
  constructor() {
    this.init()
  }

  private init(): void {
    this.strToDate()
    this.numberToDate()
    this.dateObjToDate()
  }

  // 字符串转换时间格式
  // 2019-09-09.format(fmt)
  private strToDate(): void {
    String.prototype.format = function(fmt: string = 'YYYY-MM-DD'): string | Date {
      // ios 兼容 - 替换为斜杠
      const str: string = (this as string).replace(/-/g, '/')
      let space = str.split(' ')
      let date
      let time
      if (space && space[1]) {
        date = space[0].split('/')[1] ? space[0].split('/') : ''
        time = space[1].split(':')[1] ? space[1].split(':') : ''
      } else {
        date = str.split('/')[1] ? str.split('/') : ''
        time = str.split(':')[1] ? str.split(':') : ''
      }
      let year = Number(date[0]) || new Date().getFullYear()
      let month = Number(date[1]) || new Date().getMonth()
      let day = Number(date[2]) || new Date().getDate()
      let hours = Number(time[0]) || new Date().getHours()
      let minutes = Number(time[1]) || new Date().getMinutes()
      return new Date(year, month - 1, day, hours, minutes)
    }
  }

  // 数字转换时间格式
  // 12313131323231.format(fmt)
  private numberToDate(): void {
    Number.prototype.format = function(fmt: string = 'YYYY-MM-DD'): string {
      return moment(new Date(this as number)).format(fmt)
    }
  }

  // 时间对象转换时间格式
  // new Date().format(fmt)
  private dateObjToDate(): void {
    Date.prototype.format = function(fmt: string = 'YYYY-MM-DD'): string {
      return moment(new Date(this as Date)).format(fmt)
    }
  }
}

new InPrototype()
