import Vue from 'vue'
import store from '@/store'
import { getStorage, removeStorage } from 'plugin/helper'
import { formatMapType } from 'plugin/dict'

Vue.mixin({
  methods: {
    /**
     * 触发某个组件上面订阅的事件
     * @param componentName
     * @param options
     */
    dispatch(componentName: string, options: dispatchOptions): void {
      const defaultOptions = {
        root: this.$root,
        level: Infinity,
        ...options
      }

      const { root, event, params, level } = defaultOptions

      const vnode = this.findComponentByName(componentName, root, level, 'single')[0]

      if (vnode) {
        if (typeof event === 'string') {
          vnode.$emit.apply(vnode, [event, ...params])
        } else {
          event.forEach((item) => vnode.$emit.apply(vnode, [item, ...params]))
        }
      }
    },

    /**
     * 递归查找相应组件
     * @param componentName
     * @param root
     * @param level
     */
    findComponentByName(componentName: string, root?: Vue, level?: number, type?: 'single' | 'multiple'): Array<Vue> {
      const vnodeList: Array<Vue> = []
      const _root = root || this.$root
      const _type = type || 'multiple'
      let _level = level || Infinity

      const recursion = (vnodes: Array<any>, componentName: string) => {
        let childrenList: Array<Vue> = []
        _level--

        for (let i = 0, l = vnodes.length; i < l; i++) {
          if (vnodes[i].componentName && vnodes[i].componentName === componentName) {
            vnodeList.push(vnodes[i])
          }

          if (vnodes[i].$children && vnodes[i].$children.length > 0) {
            childrenList = childrenList.concat(vnodes[i].$children)
          }
        }

        // 当 type为single时 查询到一个就不继续执行遍历了
        if (!(_type === 'single' && vnodeList.length > 0) && childrenList.length > 0 && _level > 0) {
          recursion(childrenList, componentName)
        }
      }

      recursion(_root.$children, componentName)

      return vnodeList
    },

    // 返回方法
    goBack(isHistory: boolean = false): void {
      // history 返回历史记录
      if (isHistory) {
        window.history.go(-1)
        return
      }

      const back: string = this.$route.meta.back

      // 没有back 返回上一页
      if (!back) {
        this.$router.go(-1)
        return
      }

      // 退出程序
      if (back === 'close') {
        try {
          this.$sdk.closePage()
        } catch (error) {
          this.$router.go(-1)
        }
      } else {
        // 返回指定页面
        this.$router.push({ name: back })
      }
    },

    // 根据产品id 前往首页
    goHome(proId: string, clearCache: boolean = true): void {
      if (clearCache) {
        store.dispatch('app/clearCacheView')
        removeStorage(getStorage('viewCacheStack'))
      }

      window.location.href = `${process.env.VUE_APP_HOME_URL}${proId}&timestamp=${new Date().getTime()}`
    },

    // 联系客服页面
    goService(): void {
      window.location.href = '/slime'
    },

    // type - label 字典转义
    formatMapType(type: string, value: number | string) {
      return formatMapType(type, value)
    }
  }
})
