










































































































import { Vue, Component, Mixins, Prop, Watch, Ref } from 'vue-property-decorator'
import { Getter } from 'vuex-class'
import { Icon, Checkbox, Field } from 'vant'

import baseWidget from 'components/mixins/baseWidget'
import baseValidate from 'components/mixins/baseValidate'
import baseField from 'components/mixins/baseField'

@Component({
  name: 'insidePlateNum',
  components: {
    vanIcon: Icon,
    vanCheckbox: Checkbox,
    vanField: Field
  }
})
export default class insidePlateNum extends Mixins(baseWidget, baseValidate, baseField) {
  @Ref('font') fontRef!: HTMLFormElement
  @Ref('letter') letterRef!: HTMLFormElement
  @Ref('numOne') numOneRef!: HTMLFormElement
  @Ref('numTwo') numTwoRef!: HTMLFormElement
  @Ref('numThree') numThreeRef!: HTMLFormElement
  @Ref('numFour') numFourRef!: HTMLFormElement
  @Ref('numFive') numFiveRef!: HTMLFormElement
  @Ref('numSix') numSixRef!: HTMLFormElement
  @Ref('content') contentRef!: HTMLFormElement
  @Ref('keyborard') keyborardRef!: any

  private checkbox: boolean = false
  private selected = null
  private isSelectx: boolean = false
  private isSelectl: boolean = false
  private key: any = null // 当前选中的车牌下标
  private plateValue = {
    // 车牌的当前值
    areaName: '',
    areaLetter: '',
    numOne: '',
    numTwo: '',
    numThree: '',
    numFour: '',
    numFive: '',
    numSix: ''
  }
  private keyboardShow: boolean = false // 键盘是否显示
  private isDisable: boolean = false // 是否禁用

  private get carPlateDate(): any {
    let result: any = []
    if (this.key === 1) {
      result = [
        { name: ['粤', '京', '冀', '沪', '津', '晋', '蒙', '辽', '吉', '黑'] },
        { name: ['苏', '浙', '皖', '闽', '赣', '鲁', '豫', '鄂', '湘', '桂'] },
        { name: ['琼', '渝', '川', '贵', '云', '藏', '陕', '甘', '青', '宁'] },
        { name: ['新', '临', '', '', '', '', '', '', '删除'] }
      ]
    } else if (this.key === 7 || this.key === 8) {
      result = [
        { name: ['1', '2', '3', '4', '5', '6', '7', '8', '9', '0'] },
        { name: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'J', 'K'] },
        { name: ['L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'U', 'V'] },
        { name: ['W', 'X', 'Y', 'Z', '港', '澳', '学', '挂', '删除'] }
      ]
    } else {
      result = [
        { name: ['1', '2', '3', '4', '5', '6', '7', '8', '9', '0'] },
        { name: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'J', 'K'] },
        { name: ['L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'U', 'V'] },
        { name: ['W', 'X', 'Y', 'Z', '', '', '', '', '删除'] }
      ]
    }
    return result
  }

  @Watch('value', { immediate: true })
  public valueHandler(): void {
    let str = this.value || ''
    this.plateValue.areaName = str.substring(0, 1)
    this.plateValue.areaLetter = str.substring(1, 2)
    this.plateValue.numOne = str.substring(2, 3)
    this.plateValue.numTwo = str.substring(3, 4)
    this.plateValue.numThree = str.substring(4, 5)
    this.plateValue.numFour = str.substring(5, 6)
    this.plateValue.numFive = str.substring(6, 7)
    this.plateValue.numSix = str.substring(7, 8)
  }

  @Watch('numSix')
  private numSixHandler(val: boolean): void {
    // 监听是否为新能源车牌最后一位
    if (val) {
      this.keyboardShow = false
      this.over()
    }
  }

  @Watch('keyboardShow')
  keyboardShowHandler(val: boolean) {
    this.floatMenuToggle(val)
  }

  // 控制键盘监听点击其他位置关闭
  private handler() {
    document.body.addEventListener('click', this.close, true)
  }

  public created() {
    this.handler()
  }

  public beforeDestroy() {
    this.handler()
  }

  private opened() {
    this.keyboardShow = true
  }

  private close() {
    if (this.keyboardShow) {
      this.keyboardShow = false
    }
  }

  // 格式化车牌号
  private get formatCarPlate(): string {
    let carPlate =
      this.plateValue.areaName +
      this.plateValue.areaLetter +
      this.plateValue.numOne +
      this.plateValue.numTwo +
      this.plateValue.numThree +
      this.plateValue.numFour +
      this.plateValue.numFive +
      this.plateValue.numSix
    return carPlate
  }

  // 车牌号第一位输入框选择
  private btnClickYue(key: number) {
    if (this.configDisabled) {
      return false
    }
    this.keyboardShow = true
    this.key = 1
    if (document.body.clientHeight - this.contentRef.getBoundingClientRect().top < 330) {
      this.contentRef.scrollIntoView({ block: 'center', inline: 'nearest' })
    }
  }

  // 车牌号第二位输入框选择
  private btnClickA(key: number) {
    if (this.configDisabled) {
      return false
    }
    this.isDisable = true
    this.keyboardShow = true
    this.key = 2
    if (document.body.clientHeight - this.contentRef.getBoundingClientRect().top < 330) {
      this.contentRef.scrollIntoView({ block: 'center', inline: 'nearest' })
    }
  }

  // 后五位车牌号输入框选择
  private btnClickNum(key: number) {
    if (this.configDisabled) {
      return false
    }
    this.key = key
    if (this.isSelectl) {
      this.isDisable = true
    } else {
      this.isDisable = false
    }
    this.keyboardShow = true
    if (document.body.clientHeight - this.contentRef.getBoundingClientRect().top < 330) {
      this.contentRef.scrollIntoView({ block: 'center', inline: 'nearest' })
    }
  }

  // 键盘选择显示
  private btnWordClick(rows: any, index: number, i: any): void {
    if (i === '删除') {
      this.clearClick()
    } else {
      this.selected = i
      if (this.key === 1) {
        this.plateValue.areaName = i
        this.isSelectl = false
        this.letterRef.click()
      } else if (this.key === 2) {
        this.plateValue.areaLetter = i
        this.numOneRef.click()
      } else if (this.key === 3) {
        this.plateValue.numOne = i
        this.numTwoRef.click()
      } else if (this.key === 4) {
        this.plateValue.numTwo = i
        this.numThreeRef.click()
      } else if (this.key === 5) {
        this.plateValue.numThree = i
        this.numFourRef.click()
      } else if (this.key === 6) {
        this.plateValue.numFour = i
        this.numFiveRef.click()
      } else if (this.key === 7) {
        this.plateValue.numFive = i
        this.numSixRef.click()
      } else if (this.key === 8) {
        this.plateValue.numSix = i
      }
    }

    this.updateValue(this.formatCarPlate)
  }

  // 键盘点击‘完成’
  completeClick() {
    this.keyboardShow = false
    this.over()
    this.updateValue(this.formatCarPlate)
  }

  // 键盘点击‘删除图标’
  clearClick() {
    if (this.key === 1) {
      this.plateValue.areaName = ''
    } else if (this.key === 2) {
      this.fontRef.click()
      this.plateValue.areaLetter = ''
    } else if (this.key === 3) {
      this.letterRef.click()
      this.plateValue.numOne = ''
    } else if (this.key === 4) {
      this.numOneRef.click()
      this.plateValue.numTwo = ''
    } else if (this.key === 5) {
      this.numTwoRef.click()
      this.plateValue.numThree = ''
    } else if (this.key === 6) {
      this.numThreeRef.click()
      this.plateValue.numFour = ''
    } else if (this.key === 7) {
      this.numFourRef.click()
      this.plateValue.numFive = ''
    } else if (this.key === 8) {
      this.numFiveRef.click()
      this.plateValue.numSix = ''
    }
    this.updateValue(this.formatCarPlate)
  }

  // 点击完成按钮
  private over() {
    this.$emit('saveCarPlate', this.formatCarPlate)
  }
}
