





















import { Component, Mixins, Prop, Watch } from 'vue-property-decorator'

import baseComponent from 'components/mixins/baseComponent'
import { checkSystem } from 'plugin/helper'

@Component({
  name: 'insideFormHeader'
})
export default class insdieFormHeader extends Mixins(baseComponent) {
  private get isIos(): boolean {
    return checkSystem() === 'ios' || false
  }

  private get configType(): string {
    return this.configData.type || 'primary'
  }

  private get configTitle(): string {
    return this.configData.title
  }

  private get configTags(): Array<string> {
    return this.configData.tags || []
  }

  private get configAreaValue(): boolean {
    return this.configData.areaValue || '请选择办理地区'
  }

  private get configAreaShow(): boolean {
    return this.configData.areaShow
  }
}
