




















import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
import { Icon } from 'vant'

@Component({
  name: 'insideInfoPanel',
  components: {
    vanIcon: Icon
  }
})
export default class insideInfoPanel extends Vue {
  @Prop({ type: String }) title!: string
  // 最多显示多少条
  @Prop({ type: Number, default: Infinity }) size!: number
  // 超出多少条是才隐藏
  @Prop({ type: Number, default: 2 }) clamp!: number
  //  标题右边的文案
  @Prop({ type: String }) desc!: string
  //  标题箭头是否显示
  @Prop({ type: Boolean, default: false }) link!: boolean

  // 子元素列表
  private panelChildList: Array<Vue> = []
  // 是否展开
  private isExtend: boolean = false
  // 是否超出
  private isOverflow: boolean = false
  // 默认边距
  private defaultPadding: number = 10
  private panelMainStyle = {}

  @Watch('panelChildList')
  private panelChildListHandler(): void {
    this.setPanelMainStyle()
  }

  private init(): void {
    this.$on('info.panel.add', (child: Vue) => {
      if (child) {
        this.panelChildList.push(child)
      }
    })

    this.$on('info.panel.remove', (child: Vue) => {
      if (child) {
        this.panelChildList.splice(this.panelChildList.indexOf(child), 1)
      }
    })
  }

  private computeLimitHeight(): number {
    let limitHeight: number = 0
    const panelChildList = this.panelChildList

    if (panelChildList) {
      const panelChildSize = panelChildList.length
      const overflowSize = panelChildSize - this.size
      this.isOverflow = panelChildSize > this.size && overflowSize > this.clamp

      if (this.isOverflow) {
        panelChildList.forEach((item, index) => {
          if (item.$el) {
            const computedStyle = window.getComputedStyle(item.$el)
            const top = parseFloat(computedStyle.paddingTop || '') + parseFloat(computedStyle.marginTop || '')
            const bottom = parseFloat(computedStyle.paddingBottom || '') + parseFloat(computedStyle.marginBottom || '')
            const height = parseFloat(computedStyle.height || '')
            if (index < this.size || this.isExtend) {
              limitHeight += top + bottom + height
            }
          }
        })
      }
    }

    return limitHeight
  }

  private setPanelMainStyle(): void {
    const limitHeight = this.computeLimitHeight()
    if (limitHeight) {
      this.panelMainStyle = {
        // 展开算上下边距 不展开算上边距
        height: limitHeight + (this.isExtend ? this.defaultPadding * 2 : this.defaultPadding) + 'px'
      }
    }
  }

  // 展开或隐藏
  private toggle(): void {
    if (this.isOverflow) {
      this.isExtend = !this.isExtend
      this.setPanelMainStyle()
    }
  }
  private panelRight(): void {
    this.$emit('panel-right')
  }

  private created(): void {
    this.init()
  }
}
