




























































import { Component, Prop, Watch, Mixins } from 'vue-property-decorator'
import { Field, Popup, Search, Button } from 'vant'
import { debounce, isNull } from 'plugin/helper'
import { cloneDeep } from 'lodash'

import baseWidget from 'components/mixins/baseWidget'
import baseValidate from 'components/mixins/baseValidate'
import baseField from 'components/mixins/baseField'

@Component({
  name: 'insideSelectPopup',
  components: {
    vanField: Field,
    vanPopup: Popup,
    vanSearch: Search,
    vanButton: Button
  }
})
export default class insideSelectPopup extends Mixins(baseWidget, baseValidate, baseField) {
  /**
   * 默认选中
   */
  @Prop({ type: [String, Number] }) defaultKey!: string | number
  /**
   * 额外配置
   */
  @Prop({ type: Object, default: () => ({ id: 'id', name: 'name' }) }) props!: { [k: string]: string }

  private insideOptions: Array<any> = this.configOptions || []
  private show: boolean = false
  private backShow: boolean = true
  private selectValue: string = ''

  private get idKey(): string {
    return this.props.id || 'id'
  }

  private get nameKey(): string {
    return this.props.name || 'name'
  }

  private get formatDisplayValue(): string {
    const selectList = this.insideOptions.filter((item) => item[this.idKey] === this.value)
    return selectList[0] ? selectList[0][this.nameKey] : String(this.value)
  }

  @Watch('defaultKey', { immediate: true })
  private defaultKeyHandler(): void {
    this.selectDefault()
  }

  @Watch('configOptions')
  private optionHandler(): void {
    this.insideOptions = this.configOptions
    this.selectDefault()
  }

  @Watch('selectValue')
  private selectValueHandler(): void {
    this.inputSearch()
  }

  private showSelectPopup() {
    // 只读 || 禁用
    if (this.configReadonly || this.configDisabled) {
      return false
    }

    // 如果没有数据提示
    if (!this.insideOptions.length) {
      this.$toast('暂无可选' + this.configLabel)
      return false
    }

    this.show = true
  }

  // 搜索
  private onSearch(): void {
    if (!this.selectValue) {
      this.insideOptions = this.configOptions
    } else {
      const options = cloneDeep(this.configOptions)
      this.insideOptions = options.filter((item: any) => {
        if (item.name.indexOf(this.selectValue) !== -1) {
          item.currentName = item.name.replace(
            new RegExp(this.selectValue, 'g'),
            `<span class='primary-color'>${this.selectValue}</span>`
          )
          return true
        }
      })
    }
  }

  private inputSearch(): void {
    debounce(this.onSearch, 200)()
  }

  // 选择触发
  private change(item: any): void {
    this.insideValue = item ? item[this.idKey] : ''
    this.updateValue(this.insideValue)
    this.changeValue(item)
    this.close()
  }

  // 默认选中
  private selectDefault(): void {
    if (this.value && !isNull(this.insideOptions)) {
      const item = this.insideOptions.filter((item) => item[this.idKey] === this.value)
      this.change(item[0])
    }
  }

  private closePopup(): void {
    this.selectValue = ''
    this.formParentValidate()
  }

  // 关闭
  private close() {
    this.show = false
  }
}
