import { getURLParam } from 'plugin/helper'
export default function() {
  let option = {
    cdn: 'https://cdn.jsdelivr.net/npm/vconsole@3.1.0/dist/vconsole.min.js',
    activeParams: 'vConsole', // 激活参数名称,
    loadCompleted: function() {
      const vConsole = new window.VConsole()
      vConsole.showSwitch()
    }
  }
  if (getURLParam(option.activeParams) === 'true') {
    loadScript(option.cdn, option.loadCompleted)
  }
}

function loadScript(src: string, callback: any) {
  let s: any
  let r: boolean = false
  let t: any
  s = document.createElement('script')
  s.type = 'text/javascript'
  s.src = src
  s.onload = s.onreadystatechange = function() {
    if (!r && (!this.readyState || this.readyState === 'complete')) {
      r = true
      callback()
    }
  }
  t = document.getElementsByTagName('script')[0]
  t.parentNode.insertBefore(s, t)
}
