




import { Vue, Component, Prop, Watch, Ref } from 'vue-property-decorator'
import { Popup, Picker } from 'vant'
import moment from 'moment'

@Component({
  name: 'year',
  components: {
    vanPopup: Popup,
    vanPicker: Picker
  }
})
export default class year extends Vue {
  @Ref('picker') pickerRef!: any

  @Prop({ type: String }) private readonly title!: string
  @Prop({ type: String, required: true }) readonly value!: string
  @Prop({ type: Date }) private readonly minDate!: Date
  @Prop({ type: Date }) private readonly maxDate!: Date

  private columns: Array<any> = []
  private resultValue: string = ''
  private defaultIndex: number = 0

  @Watch('minDate', { immediate: true })
  private minDateHandler(): void {
    this.setInitDateColumns()
  }

  @Watch('maxDate', { immediate: true })
  private maxDateHandler(): void {
    this.setInitDateColumns()
  }

  private setInitDateColumns(): void {
    const yearColumns: Array<string> = []

    let maxDate = new Date(this.maxDate).getFullYear()
    let minDate = new Date(this.minDate).getFullYear()
    let currentYear = new Date().getFullYear()
    while (maxDate >= minDate) {
      yearColumns.push(String(minDate))
      minDate++
    }

    this.columns = yearColumns
    if (this.value === '') {
      this.defaultIndex = this.columns.indexOf(String(currentYear))
    }
  }

  private confirm(value: Array<string>): void {
    let _value: string = ''
    _value = moment(value, 'YYYYMMDD').format('YYYY')
    this.resultValue = _value
    this.$emit('input', this.resultValue)
    this.$emit('confirm', this.resultValue)
  }

  private cancel(): void {
    this.$emit('cancel', this.resultValue)
  }
}
