


























import { Component, Watch, Mixins } from 'vue-property-decorator'
import { Field, Icon } from 'vant'

import baseWidget from 'components/mixins/baseWidget'
import baseValidate from 'components/mixins/baseValidate'
import baseField from 'components/mixins/baseField'

@Component({
  name: 'insideScanField',
  components: {
    vanField: Field,
    vanIcon: Icon
  }
})
export default class insideScanField extends Mixins(baseWidget, baseValidate, baseField) {
  @Watch('insideValue')
  insideValueHandler(): void {
    this.updateValue(this.insideValue)
  }

  async scan() {
    if (this.configDisabled) return false
    const res = await this.$sdk.scan()

    if (res) {
      let result = res.split(',')
      this.insideValue = result[result.length - 1]
    }
  }

  blur() {
    this.$emit('blur')
    this.formParentValidate()

    this.floatMenuToggle(false)
  }

  focus() {
    this.floatMenuToggle(true)
  }
}
