




























import { Vue, Component, Prop, InjectReactive } from 'vue-property-decorator'

import { Icon } from 'vant'

@Component({
  name: 'insideFormItem',
  inheritAttrs: false,
  components: {
    vanIcon: Icon
  }
})
export default class insideFormItem extends Vue {
  @Prop({ type: String }) label!: string

  @Prop({ type: Object, default: () => ({}) }) configData!: any

  @InjectReactive({ from: 'labelPosition', default: 'left' }) labelPosition!: string

  private get isNeedShowFormItemLabel() {
    return this.labelPosition === 'top'
  }

  // 是否平铺式
  private get configSmooth(): boolean {
    return this.configData.smooth || false
  }

  // 提示文案
  private get configPlaceholder(): string {
    return this.configData.placeholder || ''
  }

  private showWarningTips(tips: string) {
    if (tips) this.$toast(tips)
  }
}
