






















import { Component, Mixins, ProvideReactive } from 'vue-property-decorator'

import baseComponent from 'components/mixins/baseComponent'

@Component({
  name: 'insideFormGroup'
})
export default class insideFormGroup extends Mixins(baseComponent) {
  private get configTitle() {
    return this.configData.title
  }

  @ProvideReactive('labelPosition')
  private get configLabelPosition() {
    return this.configData.labelPosition || 'left'
  }
}
