






import { Vue, Component, Prop, Provide } from 'vue-property-decorator'
import { isNull } from 'plugin/helper'

@Component({
  name: 'globalValidate'
})
export default class globalValidate extends Vue {
  @Prop({ type: String, default: 'div' }) tag!: string

  @Provide() globalValidate = this

  // 校验项
  public validateItemList: Array<globalValidateItem> = []

  // 对在 validateItemList 中存在的校验项组件进行循环校验
  public async validate() {
    // 校验结果
    const validateResList: Array<validateRes> = await Promise.all(this.validateItemList.map((item) => item.validate()))

    const hasError = validateResList.filter((item) => item.code === 500)
    const valid = isNull(hasError)
    const errorRes = hasError[0] || { failMsg: '' }

    // 校验失败，弹框提示
    if (!valid) {
      this.$toast(errorRes.failMsg)
    }

    return { valid, errorRes, hasError }
  }

  // 对部分字段进行校验 validate-item 需要传 prop
  public async validateField(fields: Array<string>) {
    const filterValidateItemList = this.validateItemList.filter((item) => fields.includes(item.prop))

    // 校验结果
    const validateResList: Array<validateRes> = await Promise.all(filterValidateItemList.map((item) => item.validate()))

    const hasError = validateResList.filter((item) => item.code === 500)
    const valid = isNull(hasError)
    const errorRes = hasError[0] || {}

    return { valid, errorRes, hasError }
  }

  // 添加校验项
  private addValidateItem() {
    this.$on('validate.item.add', (validateItem: globalValidateItem) => {
      if (validateItem) {
        this.validateItemList.push(validateItem)

        // 获取 所有 global-validate__item
        const els = document.querySelectorAll('.global-validate__item')

        // 按照 global-validate__item 顺序 排序 this.validateItemList
        this.validateItemList.sort((a, b) => {
          let aIndex: number = 0
          let bIndex: number = 0

          els.forEach((el, index) => {
            if (a.$el === el) aIndex = index
            if (b.$el === el) bIndex = index
          })

          return aIndex - bIndex
        })
      }
    })
  }

  // 删除校验项
  private removeValidateItem() {
    this.$on('validate.item.remove', (validateItem: globalValidateItem) => {
      if (validateItem) {
        this.validateItemList.splice(this.validateItemList.indexOf(validateItem), 1)
      }
    })
  }

  private created() {
    this.addValidateItem()
    this.removeValidateItem()
  }
}
