



















































import { Mixins, Component, Ref } from 'vue-property-decorator'
import { validate } from 'plugin/validate'
import { Field, Popup } from 'vant'

import baseWidget from 'components/mixins/baseWidget'
import baseValidate from 'components/mixins/baseValidate'
import baseField from 'components/mixins/baseField'

import picker from './picker.vue'

@Component({
  name: 'insidePickerPopup',
  components: {
    vanField: Field,
    vanPopup: Popup,
    picker
  }
})
export default class insidePickerPopup extends Mixins(baseWidget, baseValidate, baseField) {
  @Ref('picker') pickerRef!: picker

  private showPopup: boolean = false

  // 是否是平铺式的
  private get configSmooth(): boolean {
    return this.configData.smooth || false
  }

  // 格式化显示内容
  private get formatLabel(): string {
    if (typeof this.value === 'string') {
      return this.value
    }

    return this.value.join('、')
  }

  // 打开弹窗时触发 重新格式化选项内容
  private openPopup() {
    this.insideValue = this.value

    if (this.pickerRef) this.pickerRef.formatInsideOptions()
  }

  // 更新值
  private change() {
    this.updateValue(this.insideValue)
    this.changeValue(this.insideValue)

    const globalPageWrapperRef = this.globalPageWrapperRef()

    if (globalPageWrapperRef) {
      globalPageWrapperRef.clearValueByShowLogic(this.field, this.insideValue)
    }
  }

  // 确认时更新值并关闭弹窗 并校验
  private onConfirm(): void {
    this.change()

    this.showPopup = false

    this.formParentValidate()
  }

  // 平铺类型的picker选择时需要校验
  private smoothPickerChange() {
    this.change()

    this.formParentValidate()
  }

  // 自定义校验 提供给 validate-item 调用
  public async customValidate(value: any, rules: Array<rule>): Promise<validateRes> {
    let validateRes: validateRes = { code: 200, failMsg: '' }

    // 常规校验流程
    validateRes = await validate(value, rules)

    // rules 校验通过，校验选项备注是否填写正确
    if (validateRes.code === 200 && this.pickerRef) {
      validateRes = await this.pickerRef.validOptionRemark()
    }

    return validateRes
  }
}
