












import { Component, Mixins, Prop } from 'vue-property-decorator'
import { Icon } from 'vant'

import baseComponent from 'components/mixins/baseComponent'

@Component({
  name: 'insideInfoDescription',
  components: {
    vanIcon: Icon
  }
})
export default class insideInfoDescription extends Mixins(baseComponent) {
  private get configContent() {
    return this.configData.content
  }
}
