import { checkValueType, isNull } from 'plugin/helper'
import { cloneDeep } from 'lodash'
import presets from './presets'

export const validate = (value: any, rules: Array<rule>): Promise<validateRes> => {
  return new Promise(resolve => {
    if (isNull(rules)) {
      resolve({ code: 200, failMsg: '' })
      return
    }

    const ruleList = cloneDeep(rules)

    const recursion = () => {
      const rule = ruleList.shift()
      const surplus = ruleList.length

      // 校验方法
      let validateRule!: (value: any, failMsg: string, content: string) => Error | undefined

      if (rule) {
        if (presets[rule.type]) validateRule = presets[rule.type]

        if (validateRule && checkValueType(validateRule, '[object Function]')) {
          try {
            const validateError = validateRule(value, rule.failMsg, rule.content || '')
            if (validateError) {
              // 如果校验失败 就 提前调用回调 退出校验
              resolve({ code: 500, failMsg: rule.failMsg })
            } else if (surplus) {
              // 如果没校验完 就 继续校验
              recursion()
            } else {
              // 如果校验通过
              resolve({ code: 200, failMsg: '' })
            }
          } catch (error) {
            console.error(error)
            resolve({ code: 500, failMsg: rule.failMsg })
          }
        }
      }
    }

    recursion()
  })
}
