import { isNull } from './type'

/**
 * 是否是外链
 */
export const isExternal = (path?: string): boolean => /^(https?:|mailto:|tel:)/.test(path || '')

/**
 * 防抖
 */
let timeout: null | number = null
export const debounce = (event: any, wait: number): ((...arg: any) => void) => {
  return (...arg: any) => {
    if (timeout !== null) clearTimeout(timeout)
    timeout = setTimeout(() => {
      event(...arg)
    }, wait)
  }
}

/**
 * 解决h5下微信环境和支付宝环境下标题不更新的问题
 */
export const setDocumentTitle = (title: string): void => {
  const titleEle: HTMLTitleElement = document.getElementsByTagName('title') && document.getElementsByTagName('title')[0]
  if (titleEle && titleEle.innerHTML) {
    titleEle.innerHTML = title
  }
}

/**
 * 检查客户端环境
 */
export const checkBrowser = (): string => {
  const ua: string = window.navigator.userAgent.toLowerCase()
  const micromessenger: RegExpMatchArray = ua.match(/MicroMessenger/i) || []
  const alipay: RegExpMatchArray = ua.match(/AliPay/i) || []
  const wxwork: RegExpMatchArray = ua.match(/wxwork/i) || []

  if (ua.indexOf('psbc') !== -1) {
    return 'PSBC'
  }

  if (micromessenger[0] === 'micromessenger' && wxwork[0] === 'wxwork') {
    return 'WXWORK'
  }

  if (micromessenger[0] === 'micromessenger') {
    return 'WECHAT'
  }

  if (alipay[0] === 'alipay') {
    return 'ALIPAY'
  }

  return ''
}

/**
 * 检查下单平台
 */
export const checkPlatform = (): string => {
  const ua: string = window.navigator.userAgent.toLowerCase()
  const micromessenger: RegExpMatchArray = ua.match(/MicroMessenger/i) || []
  const alipay: RegExpMatchArray = ua.match(/AliPay/i) || []

  if (micromessenger[0] === 'micromessenger') {
    return 'WECHAT'
  }

  if (alipay[0] === 'alipay') {
    return 'ALIPAY'
  }

  return 'DUMAPP'
}

export const checkSystem = (): string => {
  const u = navigator.userAgent
  const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1
  const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
  if (isAndroid) return 'android'
  if (isiOS) return 'ios'
  return ''
}

/**
 * 获取URL中单个参数
 */
export const getURLParam = (string: string): string => {
  const reg = new RegExp('(^|&)' + string + '=([^&]*)(&|$)', 'i')
  const hash: string = location.hash
  const search: string = location.search
  let r: RegExpMatchArray | null | undefined

  if (search) {
    r = search.substr(1).match(reg)
  }

  if (!r && hash && hash.indexOf('?') !== -1) {
    r = location.hash
      .substring(location.hash.indexOf('?'))
      .substr(1)
      .match(reg)
  }

  return r ? decodeURI(r[2]) : ''
}

/**
 * 获取URL中多个参数
 */
export const getURLMultiParam = (array: Array<string>) => {
  const params: { [k: string]: string } = {}
  array.forEach(item => {
    params[item] = getURLParam(item)
  })
  return params
}

/**
 * 获取url中所有参数
 */
export const getURLAllParam = () => {
  const hash: string = location.hash
  const search: string = location.search

  let queryParamToken = ''

  if (search && search.indexOf('?') !== -1) {
    queryParamToken = search.substr(1)
  }

  if (hash && hash.indexOf('?') !== -1) {
    queryParamToken += `&${location.hash.substring(location.hash.indexOf('?')).substr(1)}`
  }

  const queryParamTokens = queryParamToken.split('&')

  const query: { [k: string]: string } = {}

  if (queryParamTokens) {
    queryParamTokens.forEach((item: string) => {
      if (item) {
        const key: string = item.split('=')[0]
        const value: string = item.split('=')[1]
        query[key] = value
      }
    })

    return query
  }

  return query
}

/**
 * 保留小数点后几位
 * string | toFixed(num)
 * number | toFixed(num)
 */
export const toFixed = (val: string | number, num: number = 2): string => {
  const value: string | number = isNull(val) ? 0 : val
  return parseFloat(String(value)).toFixed(num)
}

// 将base64转换为file
export const base64ToFile = (base64: string, fileName: string = ''): any => {
  const arr: any = base64.split(',')
  const type = arr[0].match(/:(.*?);/)[1]
  const bstr = atob(arr[1])

  let n = bstr.length
  let u8arr = new Uint8Array(n)
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n)
  }

  return new File([u8arr], fileName, { type })
}
