import { Vue, Component, Prop } from 'vue-property-decorator'

/**
 * 自定义表单 渲染 组件
 *
 */

@Component({
  name: 'mixinsBaseComponent',
  inheritAttrs: true
})
export default class mixinsBaseComponent extends Vue {
  @Prop() value!: any

  @Prop({ type: String }) field!: string

  @Prop({ type: String }) name!: string

  @Prop({ type: String }) type!: string

  @Prop({ type: Number }) orderNum!: number

  @Prop({ type: Object, default: () => ({}) }) configData!: any
}
