import router from '@/router'
import { RouteConfig } from 'vue-router'

import layout from 'views/layout/index.vue'

export const routes: Array<RouteConfig> = [
  {
    path: '/',
    redirect: '/single-demo/index'
  },
  {
    path: '/single-demo',
    component: layout,
    children: [
      {
        path: 'index',
        name: 'index',
        meta: { title: '单页示例', floatMenu: false },
        component: () => import(/* webpackChunkName: "single-demo.index" */ 'pages/single-demo/views/index/index.vue')
      }
    ]
  }
]

router.addRoutes(routes)

export default router
