















import { Vue, Component } from 'vue-property-decorator'
import { Popover } from 'vant'

@Component({
  name: 'floatMenu',
  components: {
    vanPopover: Popover
  }
})
export default class floatMenu extends Vue {
  public componentName = 'floatMenu'

  showPopover: boolean = false

  timeout: number | null = null
  scrolling: boolean = false

  get menuActions() {
    const menuActions = [
      {
        text: '联系客服',
        icon: 'chat',
        callback: () => {
          this.goService()
        }
      },
      {
        text: '我的订单',
        icon: 'order',
        callback: () => {
          this.$router.push({ path: '/order/list' })
        }
      },
      {
        text: '返回上页',
        icon: 'reply',
        callback: () => {
          this.goBack()
        }
      }
    ]

    const { meta, params } = this.$route

    // 如果是首页 页面页数不存在为第一页 或者 页面页数为第一页 不需要返回上一页
    if (meta && meta.home && (!params.pageIndex || params.pageIndex === '1')) {
      menuActions.pop()
    }

    return menuActions
  }

  get styleByScrolling() {
    const scrolling = this.scrolling

    return { top: '100px', right: scrolling ? '-35px' : '0' }
  }

  changeScrolling(scrolling: boolean) {
    this.scrolling = scrolling

    this.timeout !== null && clearTimeout(this.timeout)
  }

  shrinkByScroll() {
    this.changeScrolling(true)

    this.timeout = setTimeout(() => this.changeScrolling(false), 800)
  }

  mounted() {
    const el = document.querySelector('.layout-main') as HTMLElement

    el && el.addEventListener('scroll', this.shrinkByScroll)

    this.$on('floatMenu.toggle', this.changeScrolling)
  }

  beforeDestroy() {
    const el = document.querySelector('.layout-main') as HTMLElement

    el && el.removeEventListener('scroll', this.shrinkByScroll)

    this.$off('floatMenu.toggle', this.changeScrolling)
  }
}
