















import { Component, Mixins, Prop, Watch, Ref } from 'vue-property-decorator'
import { Field, Button } from 'vant'

import baseWidget from 'components/mixins/baseWidget'
import baseValidate from 'components/mixins/baseValidate'
import baseField from 'components/mixins/baseField'

const typeMap: { [k: string]: string } = {
  next: 'next',
  created: 'created'
}

@Component({
  name: 'insideButton',
  components: {
    vanField: Field,
    vanButton: Button
  }
})
export default class insideField extends Mixins(baseWidget, baseValidate, baseField) {
  public componentName: string = 'button'

  private get configRound() {
    return this.configData.round
  }

  public get configSize() {
    return this.configData.size || 'normal'
  }

  private get configColor() {
    return this.configData.color || '#2284ff'
  }

  private get configToPageIndex() {
    return this.configData.toPageIndex
  }

  private get configValidateBeforeNext() {
    return this.configData.validateBeforeNext
  }

  // 点击后触发
  private clickEvent() {
    const type = typeMap[this.configType]

    if (type) {
      this.dispatch('baseOrderForm', {
        event: `order-form-${type}`,
        params: [this.configToPageIndex, this.configValidateBeforeNext]
      })
    }
  }
}
