import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const files = require.context('./modules', false, /\.ts$/)

const modules: {
  [k: string]: any
} = {}

files.keys().forEach((key: string) => {
  modules[key.replace(/(\.\/|\.ts)/g, '')] = files(key).default
})

const store = new Vuex.Store({
  modules
})

export default store
