






import { Vue, Component, Prop, Watch } from 'vue-property-decorator'

@Component({
  name: 'insideSteps'
})
export default class insideSteps extends Vue {
  // 激活哪一个step
  @Prop({ type: [Number, Array] }) active!: number | string | Array<number | string>
  // 方向
  @Prop({ type: String, default: 'horizontal' }) direction!: string

  private stepList: Array<any> = []

  @Watch('active', { immediate: true })
  private activeHandler(): void {
    this.setIsActive()
  }

  @Watch('stepList')
  private stepListHandler(): void {
    this.setIsActive()
  }

  private init(): void {
    this.$on('step.add', (step: any) => {
      if (step) {
        this.stepList.push(step)
      }
    })

    this.$on('step.remove', (step: any) => {
      if (step) {
        this.stepList.splice(this.stepList.indexOf(step), 1)
      }
    })
  }

  private setIsActive(): void {
    this.stepList.forEach((item, index) => {
      if (this.active !== undefined) {
        if (Array.isArray(this.active)) {
          item.setIsActive(this.active.includes(index || item.name))
        } else {
          item.setIsActive(index === this.active || item.name === this.active)
        }
      }
    })
  }

  private created(): void {
    this.init()
  }
}
