// 通用业务下单
import fetch from 'plugin/axios/fetch'

const config: {
  [k: string]: appConfig
} = {
  // 开发环境
  development: {
    AXIOS_URL: '/gov',
    TIME_OUT: 30000,
    APP_DOMAIN: '/gov'
  },

  // 测试环境
  test: {
    AXIOS_URL: '/gov',
    TIME_OUT: 30000,
    APP_DOMAIN: '/gov'
  },

  // 生产环境
  production: {
    AXIOS_URL: '/gov',
    TIME_OUT: 30000,
    APP_DOMAIN: '/gov'
  }
}

export default config

const setConfig = () => {
  const VUE_APP_RUN_ENV: string = process.env.VUE_APP_RUN_ENV
  const baseConfig = config[VUE_APP_RUN_ENV]

  fetch.defaults.baseURL = baseConfig.AXIOS_URL
  fetch.defaults.timeout = baseConfig.TIME_OUT
  window.APP_DOMAIN = baseConfig.APP_DOMAIN
}

setConfig()
