import { MutationTree, ActionTree, GetterTree } from 'vuex'

interface appStateInterface {
  cacheViews: Array<string>
  isNotice: boolean
}

let state: appStateInterface = {
  cacheViews: [],
  isNotice: false
}

let mutations: MutationTree<appStateInterface> = {
  // 设置缓存页面
  SET_CACHE_VIEWS(state: appStateInterface, views: Array<string>) {
    state.cacheViews = views
  },

  SET_IS_NOTICE(state: appStateInterface, isNotice: boolean) {
    state.isNotice = isNotice
  }
}

let actions: ActionTree<appStateInterface, appStateInterface> = {
  // 设置缓存页面
  addCacheView({ state, commit }, view: string) {
    const views = state.cacheViews
    if (!views.includes(view)) {
      views.push(view)
      commit('SET_CACHE_VIEWS', views)
    }
  },

  // 清空
  clearCacheView({ commit }) {
    commit('SET_CACHE_VIEWS', [])
  },

  setIsNotice({ commit }) {
    commit('SET_IS_NOTICE', true)
  }
}

let getters: GetterTree<appStateInterface, appStateInterface> = {
  cacheViews(state: appStateInterface) {
    return state.cacheViews
  },

  isNotice(state: appStateInterface) {
    return state.isNotice
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
