import mock from 'mockjs'
import config from '../config'

const VUE_APP_RUN_ENV: string = process.env.VUE_APP_RUN_ENV
const baseConfig = config[VUE_APP_RUN_ENV]

const baseUrl = baseConfig.AXIOS_URL

mock.mock(`${baseUrl}verify/queryCertificate`, 'post', (config: any) => {
  const body = JSON.parse(config.body)
  const { fieldList } = body

  const map: { [k: string]: string } = {}

  fieldList.forEach((field: any) => {
    if (field.alias) map[field.alias] = '预览测试'
  })

  return {
    code: 200,
    data: [map],
    msg: '操作成功'
  }
})

mock.mock(`${baseUrl}verify/queryData`, 'post', (config: any) => {
  const body = JSON.parse(config.body)
  const { fieldList } = body

  const dataId = 1
  const data = fieldList.map((item: any) => ({ key: item.field, value: '预览测试' }))

  return {
    code: 200,
    data: [{ dataId, data }],
    msg: '操作成功'
  }
})

mock.mock(new RegExp(baseUrl), {
  code: 200,
  data: null,
  msg: '操作成功'
})
