// 禁用某个按钮
export const disabledButton = (name?: string) => {
  if (name) {
    try {
      const el = document.querySelector(`button[disabled-${name.replace(/\//g, '')}='true']`)
      if (el) {
        el.setAttribute('disabled', 'disabled')
      }
    } catch (error) {}
  }
}

// 启用某个按钮
export const enDisabledButton = (name?: string) => {
  if (name) {
    try {
      const el = document.querySelector(`button[disabled-${name.replace(/\//g, '')}='true']`)
      if (el) {
        el.removeAttribute('disabled')
      }
    } catch (error) {}
  }
}
