import { Toast } from 'vant'

let loadingCount: number = 0

export const success = (message: string = '成功'): void => {
  Toast(message)
}

export const fail = (message: string = '错误'): void => {
  Toast(message)
}

export const loading = (isLoading: boolean, message: string = '正在加载...'): void => {
  if (isLoading) {
    Toast.loading({
      duration: 0,
      message: message,
      forbidClick: true,
      loadingType: 'spinner'
    })
    loadingCount++
  }
}

export const clear = (): void => {
  if (loadingCount > 0) loadingCount--
  if (loadingCount === 0) Toast.clear()
}
