import { Vue, Component, Inject } from 'vue-property-decorator'

/**
 * 校验
 *
 */

@Component({
  name: 'mixinsBaseValidate',
  inheritAttrs: true
})
export default class mixinsBaseValidate extends Vue {
  @Inject('globalValidate') globalValidate!: globalValidate

  /**
   * 获取父组件
   */
  public get globalValidateItem(): globalValidateItem {
    return this.$parent as globalValidateItem
  }

  /**
   * 判断父组件是否是 globalValidateItem
   */
  public get isGlobalValidateItem(): boolean {
    return this.globalValidateItem.componentName === 'globalValidateItem'
  }

  /**
   * 校验结果
   */
  public get validateRes(): validateRes {
    if (this.isGlobalValidateItem) {
      return this.globalValidateItem.validateRes
    }
    return { code: 301, failMsg: '' }
  }

  /**
   * 是否显示成功状态
   */
  public get showSuccessStatus(): boolean {
    if (this.isGlobalValidateItem) {
      return this.globalValidateItem.showSuccessStatus
    }
    return false
  }

  /**
   * 校验结果图标
   */
  public get validateIcon(): string | undefined {
    if (this.validateRes) {
      if (this.validateRes.code === 500) {
        return 'warning'
      }

      if (this.showSuccessStatus && this.validateRes.code === 200) {
        return 'checked'
      }
    }

    return undefined
  }

  // 显示错误提示
  public showWarningTips() {
    if (this.validateRes.failMsg && this.validateRes.code === 500) {
      this.$toast(this.validateRes.failMsg)
    }
  }

  /**
   * 调用 globalValidateItem 组件校验
   */
  public async formParentValidate() {
    this.isGlobalValidateItem && (await this.globalValidateItem.validate())
  }
}
