




































import { Vue, Component, Watch, Prop } from 'vue-property-decorator'
import { Field } from 'vant'
import { validate } from '@/plugin/validate'

interface insideValueInterface {
  // 选中的值
  value: string
  // 备注
  remark: string
}

@Component({
  name: 'picker',
  components: {
    vanField: Field
  }
})
export default class picker extends Vue {
  // 选择结果 无论单选、多选都是数组。
  @Prop({ type: Array }) value!: Array<string>

  // 选项
  @Prop({ type: Array }) options!: Array<pickerInterface | string>

  // 类型  single/单选 、multi/多选。默认单选
  @Prop({ type: String, default: 'single' }) type!: string

  // 是否平铺
  @Prop({ type: Boolean, default: false }) smooth!: boolean

  private insideValue: Array<insideValueInterface> = []

  private insideOptions: Array<pickerInterface> = []

  private remarkMap: { [k: string]: string } = {}

  @Watch('value', { immediate: true })
  public valueHandler() {
    let insideValue: Array<insideValueInterface> = []

    if (this.value) {
      insideValue = this.presetValue(this.value)
    }

    this.insideValue = insideValue
  }

  @Watch('insideValue', { immediate: true })
  public insideValueHandler(): void {
    this.formatInsideOptions()

    this.formatReamrkMap()
  }

  @Watch('options', { immediate: true })
  public optionHandler(): void {
    this.formatInsideOptions()
  }

  private presetValue(value: string | Array<string>): Array<insideValueInterface> {
    const _value = typeof value === 'string' ? [value] : value

    return _value.map(item => {
      // 判断是否包含括号 包含括号 括号中的内容为备注
      if (/\((.+?)\)/g.test(item)) {
        const tokens = item.split('(')
        return {
          value: tokens[0],
          remark: tokens[1].replace(/\)/, '')
        }
      }

      return { value: item, remark: '' }
    })
  }

  // 格式化选项
  public formatInsideOptions(): void {
    const insideOptions: Array<pickerInterface> = []

    this.options.forEach((option: pickerInterface | string) => {
      if (typeof option === 'string') {
        // 当前option是否选中
        const res = this.insideValue.filter((item: insideValueInterface) => item.value === option)[0]
        insideOptions.push({
          label: option,
          value: option,
          checked: !!res,
          remarkFailMsg: '',
          isRemark: false
        })
      } else {
        // 当前option是否选中
        const res = this.insideValue.filter((item: insideValueInterface) => item.value === option.value)[0]
        insideOptions.push({
          ...option,
          remarkFailMsg: '',
          checked: !!res
        })
      }
    })

    this.insideOptions = insideOptions
  }

  // 格式化备注
  public formatReamrkMap(): void {
    this.insideValue.forEach(item => {
      this.$set(this.remarkMap, item.value, item.remark)
    })
  }

  // 选择某个选项
  private selectOption(option: pickerInterface, index: number) {
    // 单选
    if (this.type === 'single') {
      this.insideOptions.forEach(item => {
        if (item.value === option.value) {
          item.checked = !item.checked
        } else {
          item.checked = false
        }
      })
    } else {
      // 多选
      this.insideOptions[index].checked = !this.insideOptions[index].checked
    }

    this.updateValue()
  }

  private remarkBlurHandler() {
    this.updateValue()
    this.$emit('remark-blur')
  }

  // 格式化结果
  private formatResult() {
    const select = this.insideOptions
      .filter(item => item.checked)
      .map(item => (this.remarkMap[item.value] ? `${item.value}(${this.remarkMap[item.value]})` : item.value))
    return select
  }

  private updateValue() {
    const result = this.formatResult()

    this.$emit('input', result)
    this.$emit('change', result)
  }

  // 校验备注是否填写正确
  public async validOptionRemark(): Promise<validateRes> {
    for (let i = 0, l = this.insideOptions.length; i < l; i++) {
      const item = this.insideOptions[i]
      // 如果 已经选中 并且备注是必填的 并且 remarkMap 中 不存在
      if (item && item.checked && item.remarkIsRequire && !this.remarkMap[item.value]) {
        item.remarkFailMsg = '格式不正确'
        return { code: 500, failMsg: '格式不正确' }
      }

      // 如果有类型需要校验 校验限制类型
      if (item.checked && item.remarkType) {
        const validateRes = await validate(this.remarkMap[item.value], [
          { type: item.remarkType, failMsg: '格式不正确' }
        ])

        if (validateRes.code !== 200) {
          item.remarkFailMsg = validateRes.failMsg
          return validateRes
        }
      }
    }

    return { code: 200, failMsg: '' }
  }
}
