













































import { Component, Prop, Watch, Mixins } from 'vue-property-decorator'
import { Field, Popup, DatetimePicker } from 'vant'

import baseWidget from 'components/mixins/baseWidget'
import baseValidate from 'components/mixins/baseValidate'
import baseField from 'components/mixins/baseField'
import year from './year.vue'

const typeFormatMap: { [k: string]: string } = {
  datetime: 'YYYY-MM-DD HH:mm',
  date: 'YYYY-MM-DD',
  time: 'HH:mm',
  'year-month': 'YYYY-MM',
  year: 'YYYY'
}

@Component({
  name: 'insideDatePopup',
  components: {
    vanField: Field,
    vanPopup: Popup,
    vanDatetimePicker: DatetimePicker,
    year: year
  }
})
export default class insideDatePopup extends Mixins(baseWidget, baseValidate, baseField) {
  private showPopup: boolean = false

  @Watch('value', { immediate: true })
  public valueHandler() {
    if (this.configType !== 'year') {
      const str: string = this.value.replace(/-/g, '/')
      if (str) {
        if (this.configType === 'time') {
          this.insideValue = this.value
        } else {
          this.insideValue = str.format(typeFormatMap[this.configType])
        }
      } else {
        if (this.configType === 'time') {
          // 时间类型给定初始时间
          this.insideValue = new Date().getHours().toString()
        } else {
          this.insideValue = new Date()
        }
      }
    } else {
      this.insideValue = this.value
    }
  }

  private showTimePopup() {
    // 只读 || 禁用
    if (this.configReadonly || this.configDisabled) {
      return false
    }
    this.showPopup = true
  }

  private get minDateNumber() {
    const str: string = this.configMinDate.replace(/-/g, '/')
    let courrentDate = new Date().getFullYear()
    if (str === '') {
      return new Date(courrentDate - 10, 0, 1)
    }
    return new Date(str)
  }

  private get maxDateNumber() {
    const str: string = this.configMaxDate.replace(/-/g, '/')
    let courrentDate = new Date().getFullYear()
    if (str === '') {
      return new Date(courrentDate + 11, 0, 1)
    }
    return new Date(str)
  }

  private getformatValue(): string | Date {
    if (!this.configType) {
      return this.insideValue.format('YYYY-MM-DD HH:mm:ss')
    } else if (this.configType === 'time' || this.configType === 'year') {
      return this.insideValue as string
    } else {
      return this.insideValue.format(typeFormatMap[this.configType])
    }
  }

  private formatter(type: string, value: string) {
    const typeMap: { [k: string]: string } = {
      year: '年',
      month: '月',
      day: '日',
      hour: '时',
      minute: '分'
    }

    return `${value}${typeMap[type] ? typeMap[type] : ''}`
  }

  private onConfirm(): void {
    this.updateValue(this.getformatValue())
    this.changeValue(this.getformatValue())
    this.showPopup = false
  }

  private closePopup(): void {
    this.formParentValidate()
  }
}
