const files = require.context('./modules', true, /index\.ts$/)

export default {
  install(Vue: Vue.VueConstructor) {
    files.keys().forEach((key: string) => {
      const modules = key.replace(/(\/index\.ts)/g, '').replace(/(\.\/)/g, '')
      const modulesName: string = `$${modules}`
      Vue.prototype[modulesName] = files(key).default
    })
  }
}
