import { isNull, toFixed } from 'plugin/helper'

interface filtersInterface {
  [k: string]: Function
}

const filters: filtersInterface = {
  format: (val: string | number | Date, fmt?: string): string | Date => {
    if (isNull(val)) {
      return ''
    }
    return val.format(fmt)
  },

  toFixed: (val: string, num: number = 2): string => {
    return toFixed(val, num)
  }
}

export default {
  install(Vue: Vue.VueConstructor) {
    Object.keys(filters).forEach((key: string) => {
      Vue.filter(key, filters[key])
    })
  }
}
