import { DirectiveOptions, DirectiveFunction } from 'vue'

interface directiveInterface {
  [k: string]: DirectiveOptions | DirectiveFunction | undefined
}

const directives: directiveInterface = {
  disabled(el, binding) {
    const urlList: Array<string> = binding.value.split(',')
    urlList.forEach((url: string) => {
      el.setAttribute(`disabled-${url.replace(/\//g, '')}`, 'true')
    })
  },

  // 拖拽
  drag(el, bind, node) {
    const { lightTouch, move } = bind.value

    const eventData = {
      isPress: false, // 是否已经长按
      pressTime: 500, // 长按时间
      timeOut: 0, // 定时器
      clientX: 0,
      clientY: 0,

      // 触摸时设置是否已经长按
      setStartPress(e: TouchEvent, el: HTMLElement) {
        // 阻止默认事件
        e.preventDefault ? e.preventDefault() : (e.returnValue = false)
        if (e.touches.length > 1) return false
        this.isPress = false
        this.timeOut = setTimeout(() => (this.isPress = true), this.pressTime)
        this.clientX = e.touches[0].clientX - el.offsetLeft
        this.clientY = e.touches[0].clientY - el.offsetTop
      },

      setMovePress(e: TouchEvent) {
        // 阻止默认事件
        e.preventDefault ? e.preventDefault() : (e.returnValue = false)
        // 滑动时如果没有长按 清空定时器

        const offsetX = Math.abs(e.touches[0].clientX - el.offsetLeft)
        const offsetY = Math.abs(e.touches[0].clientX - el.offsetLeft)

        if (offsetX > 50 || offsetY > 50) {
          clearTimeout(this.timeOut)
        }
      },

      // 清除数据
      clearData(e: TouchEvent, el: HTMLElement) {
        e.preventDefault ? e.preventDefault() : (e.returnValue = false) // 阻止默认事件
        if (this.timeOut) clearTimeout(this.timeOut)
        // 如果没有触发长按 并且有轻触事件 就触发轻触
        if (!this.isPress && lightTouch) lightTouch(el)
        // 如果触发了长按就清除
        if (this.isPress) this.isPress = false
        this.clientX = 0
        this.clientY = 0
        return false
      }
    }

    el.ontouchstart = function(e: TouchEvent) {
      eventData.setStartPress(e, el)
    }

    el.ontouchmove = function(e: TouchEvent) {
      eventData.setMovePress(e)
      // 如果没有长按 不执行移动
      if (!eventData.isPress) return false
      // 如果存在移动事件 就执行移动事件
      if (move) move()
      const maxX = document.body.offsetWidth - 50
      const maxY = document.body.offsetHeight - 50
      const minX = 0
      const minY = 0
      let left = e.touches[0].clientX - eventData.clientX
      let top = e.touches[0].clientY - eventData.clientY
      if (left > maxX) left = maxX - 10
      if (left < minX) left = minX + 10
      if (top > maxY) top = maxY - 10
      if (top < minY) top = minY + 10
      el.style.left = left + 'px'
      el.style.top = top + 'px'
    }

    el.ontouchend = function(e: TouchEvent) {
      eventData.clearData(e, el)
    }
  }
}

export default {
  install(Vue: Vue.VueConstructor) {
    Object.keys(directives).forEach((key: string) => {
      Vue.directive(key, directives[key])
    })
  }
}
