

























































import { Component, Mixins, Prop, Watch, Ref } from 'vue-property-decorator'
import { validate } from 'plugin/validate'
import { isNull } from 'plugin/helper'

import { Field, Icon } from 'vant'

import baseWidget from 'components/mixins/baseWidget'
import baseValidate from 'components/mixins/baseValidate'
import baseField from 'components/mixins/baseField'

import insideExampleDialog from '../example-dialog/index.vue'

@Component({
  name: 'insideField',
  components: {
    vanField: Field,
    vanIcon: Icon,
    insideExampleDialog
  }
})
export default class insideField extends Mixins(baseWidget, baseValidate, baseField) {
  @Ref('insideExampleDialog') insideExampleDialogRef!: insideExampleDialog

  captcha: string = ''

  getCaptchaCount: number = 0

  captchaTime: number = 0

  isValidateCaptcha: boolean = true

  // 是否需要 填写短信验证码
  get configNeedCaptcha(): string {
    return this.configData.needCaptcha || false
  }

  // 是否需要转大写
  // 需求问产品 需要转大写
  get toUpperCase(): boolean {
    // 如果 field 是身份证的 就转大写
    if (this.field && this.field.includes('idCard')) return true

    // 如果存在 身份证校验规则的 也要全转大写
    const hasIdCardRule = this.ruleList.filter(item => item.type === 'idCard')[0]

    if (!isNull(hasIdCardRule)) return true

    return false
  }

  @Watch('value', { immediate: true })
  valueHandler() {
    this.insideValue = this.toUpperCase ? String(this.value).toUpperCase() : this.value
  }

  @Watch('insideValue')
  insideValueHandler() {
    const value = this.toUpperCase ? String(this.insideValue).toUpperCase() : this.insideValue

    this.updateValue(value)
  }

  blur() {
    this.$emit('blur')
    this.validateByField()

    this.floatMenuToggle(false)
  }

  focus() {
    this.floatMenuToggle(true)
  }

  openExample() {
    if (this.configIsNeedExample) {
      this.insideExampleDialogRef.open()
    }
  }

  // 短信验证码倒计时
  startCaptchaCountDown() {
    this.captchaTime = 60

    const countDown = () => {
      const interval = setInterval(() => {
        // 0 秒清空定时器
        if (this.captchaTime === 0) {
          clearInterval(interval)
          return
        }

        // 大于0秒 递减
        if (this.captchaTime > 0) this.captchaTime--
      }, 1000)
    }

    countDown()
  }

  // 获取短信验证码
  async getCaptcha() {
    if (this.captchaTime !== 0) {
      return
    }

    const globalPageWrapperRef = this.globalPageWrapperRef()

    if (!globalPageWrapperRef) return

    // 校验输入格式是否正确
    await this.validateByField()

    if (this.validateRes.code !== 200) {
      this.$toast(this.validateRes.failMsg)
      return
    }

    // 校验通过 获取短信验证码
    const { code } = await this.$general.getPhoneVerifyCode({
      productId: globalPageWrapperRef.getProductId(),
      phone: this.value
    })

    if (code === 200) {
      this.getCaptchaCount = this.getCaptchaCount + 1
      this.$toast('短信发送成功')
      this.startCaptchaCountDown()
    }
  }

  // 校验短信验证码
  async validateCaptcha(): Promise<validateRes> {
    if (this.getCaptchaCount === 0) {
      return { code: 500, failMsg: '请先获取短信验证码' }
    }

    if (isNull(this.captcha)) {
      return { code: 500, failMsg: '请输入6位短信验证码' }
    }

    const { code, msg } = await this.$general.checkPhoneVerifyCode({ phone: this.value, verifyCode: this.captcha })

    if (code !== 200) {
      return { code: 500, failMsg: msg }
    }

    return { code: 200, failMsg: '' }
  }

  // 自定义校验 提供给 validate-item 调用
  async customValidate(value: any, rules: Array<rule>): Promise<validateRes> {
    let validateRes: validateRes = { code: 200, failMsg: '' }

    // 常规校验流程
    validateRes = await validate(value, rules)

    // rules 校验通过，判断是否还需要校验 短信验证码
    if (validateRes.code === 200 && this.configType === 'tel' && this.configNeedCaptcha && this.isValidateCaptcha) {
      validateRes = await this.validateCaptcha()
    }

    return validateRes
  }

  // 组件内校验 不校验短信验证码 当全局提交时 再校验短信验证码
  async validateByField() {
    this.isValidateCaptcha = false
    await this.formParentValidate()
    this.isValidateCaptcha = true
  }
}
