












import { Mixins, Component, Prop, Watch } from 'vue-property-decorator'
import { Cell, RadioGroup } from 'vant'

import baseWidget from 'components/mixins/baseWidget'
import baseValidate from 'components/mixins/baseValidate'
import baseField from 'components/mixins/baseField'

@Component({
  name: 'insideRadioGroup',
  components: {
    vanCell: Cell,
    vanRadioGroup: RadioGroup
  }
})
export default class insideRadioGroup extends Mixins(baseWidget, baseValidate, baseField) {
  @Watch('insideValue')
  private insideValueHandler(): void {
    const value = this.insideValue
    this.updateValue(value)
  }

  private change() {
    this.$emit('change')

    this.formParentValidate()

    const globalPageWrapperRef = this.globalPageWrapperRef()

    if (globalPageWrapperRef) {
      globalPageWrapperRef.clearValueByShowLogic(this.field, this.insideValue)
    }
  }
}
