






import { Vue, Component, Prop, Inject } from 'vue-property-decorator'
import { validate } from 'plugin/validate'
import { isNull } from 'plugin/helper'

@Component({
  name: 'globalValidateItem'
})
export default class globalValidateItem extends Vue {
  /**
   * Inject
   */
  @Inject() globalValidate!: globalValidate

  /**
   * Prop
   */
  // 渲染标签
  @Prop({ type: String, default: 'div' }) tag!: string
  // 校验规则
  @Prop({ type: Array, default: () => [] }) rules!: Array<rule>
  // 是否显示成功状态
  @Prop({ type: Boolean, default: false }) showSuccessStatus!: boolean
  // 边框类型
  @Prop({ type: String }) border!: string

  @Prop({ type: String }) prop!: string

  public componentName = 'globalValidateItem'
  public validateRes: validateRes = { code: 301, failMsg: '' }

  private get computedClass() {
    if (this.border) {
      return `custom-border__${this.border}`
    }
    return ''
  }

  // children
  private get children(): any {
    return this.$children[0]
  }

  // 校验
  public validate(): Promise<validateRes> {
    return new Promise(resolve => {
      this.$nextTick(async () => {
        // 没有子节点 无需校验
        if (!this.children) {
          resolve({ code: 200, failMsg: '' })
          return
        }

        // 如果没有校验规则 又没有自定义校验方法 则无需校验
        if (isNull(this.rules) && !this.children.customValidate) {
          resolve({ code: 200, failMsg: '' })
          return
        }

        // 如果子组件有校验方法 就调用子组件自身的校验方法
        const customValidate = this.children.customValidate || validate

        const validateRes = await customValidate(this.children.value, this.rules)

        this.validateRes = validateRes

        resolve(validateRes)
      })
    })
  }

  private mounted() {
    // 向父组件添加自身
    this.globalValidate.$emit('validate.item.add', this)
  }

  private beforeDestroy(): void {
    // 从父组件中删除自身
    this.globalValidate.$emit('validate.item.remove', this)
  }
}
